.burger {
  position: fixed;
  top: 20px;
}

.burger__button {
  display: -webkit-flex;
  display: flex;
  position: relative;
  background: rgb(0, 121, 107);
  width: 50px;
  height: 50px;
  border-radius: 50%;
  text-align: center;
  vertical-align: middle;
  transition: all 0.2s ease;
  box-shadow: -20px 27px 55px 0px rgba(0, 0, 0, 0.5);
}

.burger__button.open {
  border: 2px solid #ffffff;
  box-shadow: none;
}

.burger__button__icon {
  display: inline-block;
  position: absolute;
  background: #fff;
  margin: 0 auto;
  width: 20px;
  height: 2px;
  top: 50%;
  left: 0;
  right: 0;
}

.burger__button__icon:before,
.burger__button__icon:after {
  content: "";
  position: absolute;
  margin: 0 auto;
  background: #fff;
  width: 20px;
  height: 2px;
  left: 0;
  right: 0;
}

.burger__button__icon:before {
  bottom: 10px;
}

.burger__button__icon:after {
  top: 10px;
}

.burger__button.open .burger__button__icon {
  background: transparent;
}

.burger__button.open .burger__button__icon:before,
.burger__button.open .burger__button__icon:after {
  -webkit-transform-origin: 0 50%;
          transform-origin: 0 50%;
  width: 28.28427125px;
}

.burger__button.open .burger__button__icon:before {
  -webkit-transform: rotateZ(0.78539816rad);
          transform: rotateZ(0.78539816rad);
}

.burger__button.open .burger__button__icon:after {
  -webkit-transform: rotateZ(-0.78539816rad);
          transform: rotateZ(-0.78539816rad);
}

.burger__menu {
  list-style-type: none;
  width: 100%;
}

.burger__menu li {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  position: relative;
  visibility: hidden;
  opacity: 0;
}

.burger__menu a {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  text-decoration: none;
  letter-spacing: 0.3em;
  text-transform: uppercase;
  color: #fff;
  font-size: 4vmax;
  line-height: 8vmax;
  font-weight: 600;
  border-left: 3px solid transparent;
  padding-left: 0;
}

.burger__menu li a {
  background: #6c9;
}

.burger__button.open + .burger__menu li {
  visibility: visible;
  opacity: 1;
}

#burgerOn {
